body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #F6E3CE;
    overflow-x: hidden; 
}

.App {
    text-align: center;
    padding: 20px;
}

h1 {
    color: #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

h2 {
    color: #8181F7;
    margin-top: 20px;
    letter-spacing: 2px;
}

input, select {
    padding: 10px;
    margin: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 200px;
    font-size: 16px;
}

button {
    padding: 10px 20px;
    margin: 10px;
    background-color: #D0A9F5;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #4fa3c3;
}

table {
    margin: 20px auto;
    border-collapse: collapse;
    width: 80%;
}

th, td {
    border: 1px solid #4caf50;
    padding: 10px;
    text-align: center;
}

th {
    background-color: #4caf50;
    color: white;
}
.room-name {
    background-color: lightgray; /* Light blue background */
    padding: 10px; /* Padding for spacing */
    border-radius: 5px;
    border:1px solid  black;/* Rounded corners */
    text-align: center; /* Center the text */
    font-weight: bold; /* Make the text bold */
    text-transform: uppercase; /* Transform text to uppercase */
}
tbody tr:nth-child(even) {
    background-color: #f2f2f2; /* Alternating row colors */
}

.message {
    margin-top: 20px;
    color: red;
}
@media (max-width: 600px) {
    input, select, button {
        width: 90%; /* Lățime mai mare pentru ecrane mici */
    }

    table {
        font-size: 14px;
        margin:auto; /* Mărime text mai mică pentru ecrane mici */
    }

    /* Centrează tot ce apare deasupra tabelului */
    h1, h2, h3, select, input, button {
        text-align: center; /* Centrează textul */
        margin: 10px auto; /* Margine automată pentru a centra elementele */
        width: 80%; /* Lățimea elementelor, ajustabilă */
        max-width: 300px; /* Lățime maximă pentru a nu fi prea mari pe ecranele mari */
    }

    /* Centrează mesajul */
    .message {
        text-align: center; /* Centrează mesajele */
    }
}